      this.productSet.id = id
      this.productSet.title = title
      this.productSet.price = price
      this.updateDialog = true
    },
    // 删除
    remove(id) {
 
    },
    // 立即添加
    productAdd() {
 
    },
    // 更新产品信息
    updateProduct() {
 
    },
    // 每页大小修改
    sizeChange(val) {
      this.size = val
      if(this.searchProduct.productName !== ''){
        this.search()
      }else {
        this.getData()
      }
    },
    // 根据搜索条件查询
    search() {
      if(this.searchProduct.productName !== ''){
 
      }else {
        this.$message.error("搜索的产品名不能为空")
      }
 
    },
    // 第几页
    currentChange(val) {
      this.current = val
      if(this.searchProduct.productName !== ''){
        this.search()
      }else {
        this.getData()
      }
    },
