    getData() {
 
    }
  }
}
</script>
 
<style>
.page-row {
  margin-top: 20px;
  text-align: center;
}
 
.header2 {
  color: #004b54;
  font-size: 20px;
  position: absolute;
  left: 10px;
  top: 5px;
}
 
</style>